/* ========================================================================
 * DOM-based Routing
 * Based on http://goo.gl/EUTi53 by Paul Irish
 *
 * Only fires on body classes that match. If a body class contains a dash,
 * replace the dash with an underscore when adding it to the object below.
 *
 * .noConflict()
 * The routing is enclosed within an anonymous function so that you can
 * always reference jQuery with $, even when in .noConflict() mode.
 * ======================================================================== */

(function($) {

  // Use this variable to set up the common and page specific functions. If you
  // rename this variable, you will also need to rename the namespace below.
  var Sage = {
    // All pages
    'common': {
      init: function() {
        // JavaScript to be fired on all pages
      },
      finalize: function() {
        // JavaScript to be fired on all pages, after page specific JS is fired
        $(document).on('facetwp-loaded', function() {
          var filter_header = ".accordion-toggle";
          $( filter_header ).each(function() {
            var a_href = $(this).attr('href');
            var a_href_clean = a_href.replace('#','');
            //$(this).nextAll(':has(.card-body):first').find('.card-body').addClass('border_bottom');
          //''  $(this).next().attr('id', a_href_clean);
            $(this).closest('.panel').find('.panel-collapse').attr('id', a_href_clean);
          });
        //  if($('#popup').find('p.filled-text').length !== 0)
          var panel = ".panel";
          if($(panel).find('.facetwp-facet-price').length > 0) {
            $('.facetwp-facet-price').parent().parent().parent().addClass('grey');
           }

        $('li.breadcrumb-item:contains("Page")').remove();
        $('li.breadcrumb-item:contains("")').remove();
        $('.facetwp-depth').hide();
        $('.facetwp-checkbox.checked').each(function() {
            $(this).parents('.facetwp-depth').show();
            $(this).next('.facetwp-depth').show();
        });

        $( 'a[href="#"]' ).click( function(e) {
           e.preventDefault();
        } );
        var qs = FWP.build_query_string();
          if ( '' === qs ) { // no facets are selected
              $('.reset-btn').hide();
          }
          else {
              $('.reset-btn').show();
          }

    });
    new Breakpoints({
          mobile: {
              min: 0,
              max: 767
          },
          tablet: {
              min: 768,
              max: 991
          },
          destop: {
              min: 992,
              max: Infinity
          }
      });
    Breakpoints.on('mobile', {
enter: function() {
    console.info('enter '+ this.name);
    $(".sidebar").addClass("collapse");
},
leave: function() {
    console.info('leave '+ this.name);
    $(".sidebar").removeClass("collapse");
}
});
      }
    },
    // Home page
    'home': {
      init: function() {
        // JavaScript to be fired on the home page


      },
      finalize: function() {
        // JavaScript to be fired on the home page, after the init JS

        var recentScroll = false;
        var pos = 0;
        var sec = $('section');
        var link = $('.list-group-item-action');
        var len = sec.length;

        function up() {
          sec.removeClass('active');
          link.removeClass('border_bottom');
          if (pos === 0) {
            pos = len - 1;
            sec.eq(pos).addClass('active');
            link.eq(pos).addClass('border_bottom');
          } else {
            pos -= 1;
            sec.eq(pos).addClass('active');
            link.eq(pos).addClass('border_bottom');
          }
        }
        function down() {
          sec.removeClass('active');
          link.removeClass('border_bottom');
          if (pos === len - 1) {
            pos = 0;
            sec.eq(pos).addClass('active');
            link.eq(pos).addClass('border_bottom');
          } else {
            pos += 1;
            sec.eq(pos).addClass('active');
            link.eq(pos).addClass('border_bottom');
          }
        }
        $('body').on('mousewheel DOMMouseScroll touchmove', function(e){
          if(typeof e.originalEvent.detail === 'number' && e.originalEvent.detail !== 0) {
        if(e.originalEvent.detail > 0) {
        down();
        } else if(e.originalEvent.detail < 0){
        up();
        }
      } else if (typeof e.originalEvent.wheelDelta === 'number') {
        if(e.originalEvent.wheelDelta < 0) {
        down();
        } else if(e.originalEvent.wheelDelta > 0) {
        up();
        }
        }

        });

        $(".list-group-item-action").on('click', function(e) {
            e.preventDefault();
            var target = $(this).attr('href');
            $( "section" ).removeClass( "active" );
            $( ".list-group-item-action" ).removeClass( "border_bottom" );
            $(target).addClass('active');
            $(this).addClass('border_bottom');
         });




      }
    },
    // About us page, note the change from about-us to about_us.
    'about_us': {
      init: function() {
        // JavaScript to be fired on the about us page
      }
    },
    'post_type_archive_portfolio': {
      init: function() {
        // JavaScript to be fired on the about us page

      },
      finalize: function() {
        // JavaScript to be fired on all pages, after page specific JS is fired



        $(document).on('facetwp-loaded', function() {
          if (FWP.loaded) {
            $grid = $('.grid');
            $grid.isotope('destroy');
            $grid.isotope({
              itemSelector: '.grid-item',
              masonry: {
                columnWidth: 360
              }
            });
          }
        });
      }
    },
    'post_type_archive_product': {
      init: function() {
        // JavaScript to be fired on the about us page

      }
    },
    'single_product': {
      init: function() {
        // JavaScript to be fired on the about us page
        
      }
    },
    'single_portfolio': {
      init: function() {
        // JavaScript to be fired on the about us page

        function deferVideo() {

            //defer html5 video loading
          $("video source").each(function() {
            var sourceFile = $(this).attr("data-src");
            $(this).attr("src", sourceFile);
            var video = this.parentElement;
            video.load();
            // uncomment if video is not autoplay
            //video.toggleMute();
            $(video).on("click", function(e){
              video.muted = !video.muted;
            });

          });

        }
        window.onload = deferVideo;

        $(document).on("click", '[data-toggle="lightbox"]', function(event) {
          event.preventDefault();
          $(this).ekkoLightbox({
                alwaysShowClose: false,
                showArrows: true
            });
        });



        var fwpScrollToBtn = false;
        var $grid = $('.gallery');
        $grid.imagesLoaded(function() {
          //* Wrapped in a short timeout function because $grid.imagesLoaded doesn't reliably lay out correctly
          setTimeout(function(){
            $grid.isotope({
              itemSelector: '.gallery-item',
              masonry: {
                columnWidth: 370
              }

            });
          }, 250);
        });



      }
    },
    'single_post': {
      init: function() {
        // JavaScript to be fired on the about us page
              var fwpScrollToBtn = false;
              var $grid = $('.gallery');
              $grid.imagesLoaded(function() {
                //* Wrapped in a short timeout function because $grid.imagesLoaded doesn't reliably lay out correctly
                setTimeout(function(){
                  $grid.isotope({
                    itemSelector: '.gallery-item',
                    masonry: {
                      columnWidth: 370
                    }

                  });
                }, 250);
              });
      }
    }
  };

  // The routing fires all common scripts, followed by the page specific scripts.
  // Add additional events for more control over timing e.g. a finalize event
  var UTIL = {
    fire: function(func, funcname, args) {
      var fire;
      var namespace = Sage;
      funcname = (funcname === undefined) ? 'init' : funcname;
      fire = func !== '';
      fire = fire && namespace[func];
      fire = fire && typeof namespace[func][funcname] === 'function';

      if (fire) {
        namespace[func][funcname](args);
      }
    },
    loadEvents: function() {
      // Fire common init JS
      UTIL.fire('common');

      // Fire page-specific init JS, and then finalize JS
      $.each(document.body.className.replace(/-/g, '_').split(/\s+/), function(i, classnm) {
        UTIL.fire(classnm);
        UTIL.fire(classnm, 'finalize');
      });

      // Fire common finalize JS
      UTIL.fire('common', 'finalize');
    }
  };

  // Load Events
  $(document).ready(UTIL.loadEvents);

})(jQuery); // Fully reference jQuery after this point.
